<template>
    <div>
        <b-card class="mx-1 my-1" style="max-width:14rem;"
                img-top 
                img-alt="Image"
                img-fluid
                :img-src="this.PosterIMG"
                :title="this.MovieTitle"
                @click="showModal">
            <p class="card-text">
                A Movie!
            </p>
        </b-card>
        <b-modal ref="modalRef" 
                 size="lg" lazy hide-header hide-footer>
            <b-container>
                <b-img class="mt-3" 
                        fluid thumbnail :src="this.PosterIMG"/>
                <h1 class="mx-2 mt-2 mb-3" style="display:inline-block;">{{ MovieTitle }}</h1><b-badge variant="info">{{ Genre }}</b-badge>
                <p class="my-1"><strong>PlayTime:</strong> {{ PlayTime }}</p>
                <p class="my-1"><strong>Released Date:</strong> {{ ReleaseDate }}</p>
                <p class="my-1"><strong>Director:</strong> {{ Director }}</p>
                <p class="my-1"><strong>Actors:</strong> {{ Actors }}</p>
                <p class="mt-1 mb-2"><strong>Description:</strong></p>
                <pre class="mx-2 mt-1 mb-3">{{ Description }}</pre>
                <div>
                    <p class="mb-1"><strong>Comments:</strong></p>
                    <b-card-group class="my-3">
                        <b-card v-for="comment in comments" :key="comment.CommentID">
                            <strong class="mr-4">{{ comment.CustomerID }}</strong> {{ comment.Content }}
                        </b-card>
                        <b-button class="mt-1" v-if="!isEnd"
                                block variant="secondary">See more</b-button>
                    </b-card-group>
                    <b-form @submit="AddComment">
                        <b-form-textarea id="textarea1"
                                            no-resize :rows="3" :max-rows="5"
                                            v-model="commentContent"/>
                        <b-form-radio-group class="my-1"
                                            required buttons button-variant="outline-warning"
                                            v-model="commentRate">
                            <b-form-radio v-for="n in 10" :key="n" :value="n">{{ n }}</b-form-radio>
                        </b-form-radio-group>
                        <b-button class="my-1 float-right" 
                            type="submit" variant="primary">Comment</b-button>
                    </b-form>
                </div>
            </b-container>
            <b-btn class="mt-3" 
                block variant="outline-danger"
                @click="hideModal">Close</b-btn>
        </b-modal>
    </div>
</template>

<script>
export default {
    props: [
        'MovieID',
        'MovieTitle',
        'ReleaseDate',
        'PlayTime',
        'Genre',
        'Director',
        'Actors',
        'Description',
        'PosterIMG'
    ],
    data() {
        return {
            comments: [],
            loadPoint: 1,
            loadCount: 10,
            isEnd: false,
            commentRate: 0,
            commentContent: '',
        }
    },
    methods: {
        showModal() { 
            this.$refs.modalRef.show(); 
            this.BringComments();
        },
        hideModal() { this.$refs.modalRef.hide(); },
        AddComment(evt) {
            evt.preventDefault();
            this.$http.post('api/comment', {
                movieID: this.MovieID,
                rate: this.commentRate,
                content: this.commentContent
            }, {
                headers: {
                    'Authorization': `Bearer ${sessionStorage['token']}`
                }
            }).then((res) => {
                this.ResetComments();
                this.BringComments();
            })
        },
        ResetComments() {
            this.comments = [];
            this.loadPoint = 1;
            this.loadCount = 10;
        },
        DeleteComment() {
        },
        BringComments() {
            if (this.isEnd) return;
            this.$http.get('api/comment', {
                params: {
                    movieID: this.MovieID,
                    from: this.loadPoint,
                    to: this.loadPoint + this.loadCount - 1,
                }
            }).then((res) => {
                let data = res.data
                if (data.length > 0) {
                    this.comments.push(...data)
                    this.loadPoint += data.length;
                }
                if (data.length < this.loadCount || data.length <= 0) {
                    this.isEnd = true;
                }
            })
        }
    }
}
</script>



